body,html{
  padding:0;
  height:100%;
}

#wrapper {
  position: relative;
  height:100%;
}

#copy, #logo {
  opacity: 1;
  position: absolute;
  margin:auto;
  max-height: 300px;
  top:0;
  right:0;
  bottom:0;
  left:0;
}

#main {
  width: 100%;
  height:auto;
}


#photo {
  display:flex;
  height:350px;

  align-items: center;
  justify-content: center;
}

#photo > #photo-center {
  width:60%;
  position:relative;
  margin: 20px 0px;
}
#large{
    width:100%;
  }

:root {
  --incline-angle: 12deg; 
}

#small{
  opacity:1;
  position:relative;
  max-height: 300px;
  left:0;
  top:0;
  z-index:1;
}

#small_1{
  opacity:1;
  position:relative;
  max-height: 300px;
  left:0;
  top:0;
  z-index:1;
}

#small_2{
  opacity:1;
  position:relative;
  transform: rotate(8deg);
  max-height: 300px;
  left:0;
  top:0;
}

#small_3{
  opacity:1;
  position:relative;
  transform: rotate(-6deg);
  max-height: 300px;
  left:0;
  top:0;
}